
import React, {ReactNode} from 'react';
import ReactDOM from 'react-dom';
import 'antd/dist/antd.css';
import { Table, Tag, Space } from 'antd';
import './TableMatch.scss';

export interface TableMatchProps {
  children?: ReactNode;
  group?: string;
  className?: string;
  verbose?: boolean;
}

interface Player {
  name: string;
  group: string;
}

interface Matches {
  p1: string;
  p2: string;
  s1: string;
  s2: string;
}

const tenistas = require("../../data/players.json") as Player[];
const matches = require("../../data/matches.json") as Matches[];
export function TableMatch({ children, className, verbose, group }: TableMatchProps) {

  const players = tenistas.filter(tenista => tenista.group == group).sort((a, b) => a.name == b.name ? 0 : a.name < b.name ? -1 : 1);
  return (
    <table className="rotated-header">
    <thead>
      <tr>
        <th></th>
        {
          players.map(player => 
            <th>
              <div className="rotated-header-container">
                <div className="rotated-header-content">{player.name}</div>
              </div>
            </th>
          )
        }
      </tr>
    </thead>
    <tbody>
      {
        players.map((player, i) => 
          <tr>
            <td className="row-header">{player.name}</td> 
            {
              [0, 1, 2, 3].map(j => {
                return(
                  <td className={i == j ? "invalid" : ""}>
                    {
                      matches.filter(match => (match.p1 === players[i].name && match.p2 === players[j].name) || 
                                              (match.p2 === players[i].name && match.p1 === players[j].name)).map(match => {
                          return match.p1 === players[i].name 
                            ? `${match.s1} - ${match.s2}`
                            : `${match.s2} - ${match.s1}`;
                        })
                    }
                  </td>);
              })
            }
          </tr>
        )
      }
    </tbody>
  </table>
  );
}