
import React, {ReactNode} from 'react';
import ReactDOM from 'react-dom';
import 'antd/dist/antd.css';
import { Table, Tag, Space } from 'antd';

const columns = [
  {
    title: '#',
    dataIndex: 'pos',
    key: 'pos',
  },
  {
    title: 'Tenista',
    dataIndex: 'tenista',
    key: 'tenista',
  },
  {
    title: 'V',
    dataIndex: 'v',
    key: 'v',
  },
  {
    title: 'D',
    key: 'd',
    dataIndex: 'd',
  },
  {
    title: 'GV',
    dataIndex: 'gv',
    key: 'gv',
  },
  {
    title: 'GP',
    dataIndex: 'gp',
    key: 'gp',
  },
  {
    title: 'SG',
    dataIndex: 'sg',
    key: 'sg',
  },
];


export interface TableGroupProps {
  children?: ReactNode;
  group?: string;
  className?: string;
  verbose?: boolean;
}
interface Player {
  name: string;
  group: string;
}

interface Matches {
  p1: string;
  p2: string;
  s1: string;
  s2: string;
}
const tenistas = require("../data/players.json") as Player[];
const matches = require("../data/matches.json") as Matches[];

export function TableGroup({ children, className, verbose, group }: TableGroupProps) {
  const data = tenistas.filter(tenista => tenista.group == group).map(tenista => {
    const getWins = (name: String) => {
      let wins = 0;
      matches.filter(match => match.p1 === name || match.p2 === name).forEach(match => {
        wins += + (match.p1 === name ? match.s1 > match.s2 : match.s2 > match.s1);
      });
      return wins;
    };
    const getLosses = (name: String) => {
      let losses = 0;
      matches.filter(match => match.p1 === name || match.p2 === name).forEach(match => {
        losses += + (match.p1 === name ? match.s1 < match.s2 : match.s2 < match.s1);
      });
      return losses;
    }; 
    const getGameWins = (name: String) => {
      let wins = 0;
      matches.filter(match => match.p1 === name || match.p2 === name).forEach(match => {
        wins += + (match.p1 === name ? match.s1 : match.s2);
      });
      return wins;
    };
    const getGameLosses = (name: String) => {
      let wins = 0;
      matches.filter(match => match.p1 === name || match.p2 === name).forEach(match => {
        wins += + (match.p1 === name ? match.s2 : match.s1);
      });
      return wins;
    };
    return {
      key: tenista.name,
      tenista: tenista.name,
      pos: 1,
      v: getWins(tenista.name),
      d: getLosses(tenista.name),
      gv: getGameWins(tenista.name),
      gp: getGameLosses(tenista.name),
      sg: getGameWins(tenista.name) - getGameLosses(tenista.name)
    }
  }).sort((a, b) => {
    if (a.v != b.v) {
      return a.v > b.v ? -1 : 1;
    }
    if (a.sg != b.sg) {
      return a.sg > b.sg ? -1 : 1;
    }
    return 0;
  });
  for (let i = 0; i < 4; i++) {
    data[i] = {...data[i], pos: i+1};
  }
  
  console.log(data);
  return (
    <Table columns={columns} dataSource={data} pagination={false} />
  );
}